<template>
  <div class="order-tab">
		<div class="text-box" v-for="(item,index) in textArr" :key="index" @click="chanageIndex(index)">
			<span :class="{active:currentIndex==index}">{{item}}</span>
		</div>
  </div>
</template>

<script>
  export default {
    name: "OrderTab",
	data(){
		return{
			currentIndex:0
		}
	},
	props:{
		textArr:{
			type:Array,
			default(){
				return []
			}
		}
	},
	methods:{
		chanageIndex(index){
			this.currentIndex=index
			//发送出去
			this.$emit("orderTabClick",index);
		}
	}
  }
</script>

<style scoped>
.order-tab{
	display:flex;
	border-bottom:1px solid #f1f1f1;
	text-align:center;
	font-size:14px;
}
.text-box{
	flex:1;
	line-height:48px;
	height:48px;
}
.active{
	color:#ff5a5a;
	font-weight:bold;
	border-bottom:2px solid #ff5a5a;
	padding-bottom:4px;
}
</style>
