<template>
	<div id="regisger-and-sell">
		<!--顶部菜单-->
		<nav-bar class="home-nav-bar">
			<div slot="center">
				登记出售
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<!--菜单点击按钮-->
		<order-tab :text-arr="test" @orderTabClick="orderTabClick" class="order-tab"/>
				<!--滚动--->
		<scroll class="home-scroller"
				ref="scroller"
				:probe-type=3
				@pullingup="loadMove"
		>
			<!--菜单显示内容-->
			<order-content :test="ordersArr"/>
		</scroll>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import OrderTab from 'components/contents/orderTab/OrderTab'
import OrderContent from './childComps/OrderContent'
import Scroll from 'components/common/scroll/Scroll'

export default {
  name: 'RegisgerAndSell',
  components: {
	NavBar,
	OrderTab,
	OrderContent,
	Scroll
  },
  data(){
	return{
		title:'订单',
		test:['上架中','已下架','已售出','全部'],
		cindex:0,
		currentType:'tohdayOrder',			//		waitSendGoods,waitPayMoney,allOrders
	}
  },
  methods:{
	orderTabClick(index){
		console.log(index)
		this.cindex=index
		switch(index){
			case 0:
				this.currentType="tohdayOrder"
				break
			case 1:
				this.currentType="waitSendGoods"
				break
			case 2:
				this.currentType="waitPayMoney"
				break
			case 3:
				this.currentType="allOrders"
				break
		}
		let product={
			menpai:'化生???',level:69,serve:'一区-再续前缘',price:3687,oid:1,
		}
		this.$store.dispatch('addOrders',product).then(res=>{
			console.log(res)
		})
		
		this.$refs.scroller.finishPullUp()	//获取最新高度
	},
	loadMove(){
		//console.log("加载更多")
		this.$refs.scroller.refresh();		//刷新一下
		//this.$refs.scroller.finishPullUp()	//获取最新高度
	}
  },
  created(){
	//console.log(this.$store.state.OrdersList)
  },
  computed:{
	ordersArr(){
		return this.$store.state.OrdersList[this.currentType].list
	}
  }

}
</script >

